import React, { useContext } from 'react';
import { useNavigate, Link, NavLink } from "react-router-dom";
import { UserContext } from "../../App";

export default function AdminMenu() {
    let navigate = useNavigate();
    const {adm, dispadm} = useContext(UserContext);
    const adminlogOut = () => {
        localStorage.removeItem("admin");
        dispadm({type:"USER", payload:false});
        navigate("/");
    };
    return (
        <div className="list-group" role="tablist">
            <NavLink to="/admindashboard" className="list-group-item list-group-item-action" data-toggle="list" role="tab">Dashboard</NavLink>
            <NavLink to="/addproperty" className="list-group-item list-group-item-action" data-toggle="list" role="tab">Add Property</NavLink>
            <NavLink to="/editproperty" className="list-group-item list-group-item-action" data-toggle="list" role="tab">Edit Property</NavLink>
            <NavLink to="/offered" className="list-group-item list-group-item-action" data-toggle="list" role="tab">Offerd</NavLink>
            <NavLink to="/inquiries" className="list-group-item list-group-item-action" data-toggle="list" role="tab">Inquiries</NavLink>
            <NavLink to="/contacts" className="list-group-item list-group-item-action" data-toggle="list" role="tab">Contacts</NavLink>
            <a onClick={adminlogOut} className="list-group-item list-group-item-action cursor" data-toggle="list">Logout</a>
        </div>
    )
}
