import React, { useEffect, useState } from 'react';
import { Link } from "react-router-dom";
import axios from 'axios';
import AdminMenu from './AdminMenu';

export default function Admindashboard() {
    const [adminDet, setAdminDet] = useState({
        offerLst: '',
        inqLst: '',
        contLst: ''
    });
    const [servErr, setSrvErr] = useState(false);
    useEffect(() => {
        let token = localStorage.getItem("admin");
        if (token) {
            axios.get('api/admindash', {
                headers: {
                    "x-access-token" : token,
                    "Content-Type" : "application/json"
                }
            }).then((res) => {
                setAdminDet({
                    offerLst: res.data.offerList,
                    inqLst: res.data.inqList,
                    contLst: res.data.contList
                })
            })
            .catch((err) => {
                setSrvErr(true);
            });
        }
    }, []);
    return (
        <div className="admin-pages container">
            <div className="row">
                <div className="col-md-3">
                    <AdminMenu />
                </div>
                {!servErr?<div className="col-md-9">
                    <h3 className="mb-4">Dashboard</h3>
                    <div className='row'>
                        <div className='col-md-4'>
                            <div className='admin_boxes'>
                                <span className='admin_count'>{adminDet.offerLst}</span>
                                Unread Offered from users
                                <div className="admin_dash_quote"><Link to="/offered" className="readmore-btn">View All</Link></div>
                            </div>
                        </div>
                        <div className='col-md-4'>
                            <div className='admin_boxes'>
                                <span className='admin_count'>{adminDet.inqLst}</span>
                                Unread Inquiries from users
                                <div className="admin_dash_quote"><Link to="/inquiries" className="readmore-btn">View All</Link></div>
                            </div>
                        </div>
                        <div className='col-md-4'>
                            <div className='admin_boxes'>
                                <span className='admin_count'>{adminDet.contLst}</span>
                                Unread Contacts from users
                                <div className="admin_dash_quote"><Link to="/contacts" className="readmore-btn">View All</Link></div>
                            </div>
                        </div>
                    </div>
                    <div>&nbsp;</div>
                    <div className='row'>
                        <div className='col-md-6'>
                            <Link to="/addproperty" className="btn btn-outline-secondary btn-block">Add Property</Link>
                        </div>
                        <div className='col-md-6'>
                            <Link to="/editproperty" className="btn btn-outline-secondary btn-block">Edit Property</Link>
                        </div>
                    </div>
                </div>:<div className="col-9">
                    <div className="error-page text-center">
                        <div style={{height:"60px"}}>&nbsp;</div>
                        <h5 className="not_fnd_hd">OOPS... Something went wrong!</h5>
                        <p className="not_fnd_body">
                            Please try again later or<br />
                            <Link to="/" className="readmore-btn">Back to Home Page</Link>
                        </p>
                    </div>
                </div>}
            </div>
        </div>
    )
}
